.cb3-holder {
  position: fixed;
  bottom: 1.25rem;
  right: 1.25rem;
  background-color: rgb(29, 40, 51);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: white;
  height: 64px;
  width: 64px;
  border-radius: 25rem 25rem 4rem 25rem;
}

div#cb3:before {
  content: "";
  position: absolute;
  display: block;
  height: 30px;
  width: 30px;
  background-color: #f44336;
  right: 40px;
  bottom: 40px;
  border-radius: 100%;
}
